import { _decorator, Component, Node, resources, Prefab, instantiate, Layers, find } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('blockControlLeftManeger')
export class blockControlLeftManeger extends Component {
    @property
    currentBlockNum:number = 0;

    @property
    currentBlockNumR:number = 0;

    start() {
        this.node.on(Node.EventType.MOUSE_DOWN,this.addBlockControl,this)
    }

    update(deltaTime: number) {
        
    }
    addBlockControl(){

        if(this.node.name ="blockControl"){
            if(this.currentBlockNum <= 2){
                console.log("正在添加block")
                this.createBlock();
            }else{
                Error("已到最大数量值")
            }
        }else if (this.node.name ="blockControl"){
            if(this.currentBlockNumR <= 2){
                console.log("正在添加block")
                this.createBlockRight();
            }else{
                Error("已到最大数量值")
            }
        }
        
        
        
    }
    createBlock(){
        let that = this;
        resources.load("perfabs/left",Prefab,(err,Prefab)=>{
            if(!Prefab){
                
            }else{
                let node = instantiate(Prefab);
                node.layer = Layers.Enum.UI_2D;
                let x = that.currentBlockNum * 110;
                node.setPosition(-x,0,0);
                node.parent = find("Canvas/blockLeft");
                that.currentBlockNum +=1;
            }
        })
    }

    createBlockRight(){
        let that = this;
        resources.load("perfabs/right",Prefab,(err,Prefab)=>{
            if(!Prefab){
                
            }else{
                let node = instantiate(Prefab);
                node.layer = Layers.Enum.UI_2D;
                let x = that.currentBlockNumR * 110;
                node.setPosition(x,0,0);
                node.parent = find("Canvas/blockRight");
                that.currentBlockNumR +=1;
            }
        })
    }
}


